import * as React from 'react';
import { navigate } from 'gatsby';
import MuiLink from '@mui/material/Link';

export const Link = ({ children, href }) => {
  const internal = /^\/(?!\/)/.test(href);

  return (
    <MuiLink
      href={href}
      target={internal ? '_self' : '_blank'}
      onClick={e => {
        if (internal) {
          e.preventDefault();
          navigate(href);
        }
      }}
    >
      {children}
    </MuiLink>
  );
};
